<template>
    <van-dialog
        v-model="open"
        :title="title"
        :show-confirm-button="false"
        :show-cancel-button="false"
        :close-on-click-overlay="true"
        @close="onClose"
    >
        <template slot="title">
            <div class="ui-kit-dialog-title">
                <h2>{{title}}</h2>
                <div class="ui-kit-dialog-close" @click="onClose"><van-icon name="cross" /></div>
            </div>
        </template>
        <slot name="default"></slot>
    </van-dialog>
</template>

<script>
export default {
    name: 'UiKitDialog',
    model: {
      prop: 'visible',
      event: 'change'
    },
    props: {
        visible: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        }
    },
    data() {
      return {
          open: false
      }
    },
    watch: {
        open(v) {
            this.$emit('change', v)
        },
        visible(v) {
            this.open = v
        }
    },
    created() {
        this.open = this.visible
    },
    methods: {
        onClose() {
            this.$emit('close')
            this.$emit('change', false)
        }
    }
}
</script>

<style lang="less">
.ui-kit-dialog-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 12px;
    color: @textColor;
    .ui-kit-dialog-close {
        cursor: pointer;
    }
}
</style>
